@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,200;1,300;1,400;1,500;1,600;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Source Code Pro', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.mainHome {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  
.mainHomeBox > h1 {
  font-weight: 500;
  font-size: 2.8rem;
}

.mainHomeBox > span {
  font-weight: 300;
  font-size: 1.4rem;
}

.mainHomeBoxPulse {
  border-right: solid 1px #fff;
  margin-left: 0px;
}

.mainHomeBoxPulse.active {
  border-right: solid 1px #000;
}

.mainHome > button {
  position: absolute;
  right: 20px;
  bottom: 20px;
  height: 30px;
  padding: 0 15px;
  border-radius: 15px;
  background-color: #fff;
  border: solid 1px gray;
  font-weight: 700;
  color: #000;
  cursor: pointer;
  transition: all .2s ease-in-out;
}

.mainHome > button:hover {
  border-color: #000;
}

.mainApi {
    height: 100%;
    width: 100%;
    padding: 100px;
    position: relative;
    flex-direction: column;
    display: flex;
    flex-wrap: wrap;
    grid-gap: 100px;
    gap: 100px;
}

.mainApiHeader {
    /* width: 50%; */
    flex: 1 1;
    /* max-width: 50%; */
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.mainApiHeader > div {
    width: 100%;
}

.mainApiHeaderURL {
    display: flex;
    flex-direction: column;
}

.mainApiHeaderURL > input {
    width: 100%;
    padding: 5px;
    border: solid 1px gray;
}

.mainApiHeaderMethods {
    display: flex;
    flex-direction: row;
    grid-gap: 20px;
    gap: 20px;
    align-items: center;
}

.mainApiHeaderMethods div {
    background-color: #fff;
    border: solid 2px gray;
    padding: 0px 10px;
    cursor: pointer;
    flex: 1 1;

}

.mainApiHeaderMethods div.active {
    border-color: #000;
}

.mainApi textarea {
    width: 100%;
    height: 200px;
    resize: none;
    padding: 10px;
}

.mainApiHeader > button {
    height: 30px;
    padding: 0 15px;
    border-radius: 15px;
    background-color: #fff;
    border: solid 1px gray;
    font-weight: 700;
    color: #000;
    cursor: pointer;
    transition: all .2s ease-in-out;
}

.mainApiHeader > button:hover {
    border-color: #000;
}

.mainApiBackButton {
    position: absolute;
    right: 20px;
    bottom: 20px;
    height: 30px;
    padding: 0 15px;
    border-radius: 15px;
    background-color: #fff;
    border: solid 1px gray;
    font-weight: 700;
    color: #000;
    cursor: pointer;
    transition: all .2s ease-in-out;
}
  
.mainApiBackButton:hover {
    border-color: #000;
}

.mainApp {
  width: 100vw;
  height: 100vh;
  position: relative;
}
